
.clients_container {
	max-width: 90%;
	margin: 0 auto;
	padding: 2rem;
	text-align: center;
	min-height: 700px;
}




.clients_header{
	
	margin: 0 auto;
	padding: 3%;
	max-width: 90%;
}

.addBtn{
	cursor: pointer;
	margin-left: 12px;
	border-radius: 13px;
	padding: 6px 9px;
	font-size: 16px;
	line-height: 18px;
	background-color: transparent;
	will-change: filter;
}

.addBtn:hover{
	color: #F3F3F3;
	background-color: #3d3d3d;
	--add-btn_hover : background-color  250ms ease-in, color  250ms ease-in;
	transition: var(--add-btn_hover);
	filter: drop-shadow(0 0 2em #3d3d3d);
}

.purple.addBtn{
	color: #646cff;
}

.purple.addBtn:hover{
	color: #F3F3F3;
	background-color: #646cff;
	filter: drop-shadow(0 0 2em #646cff);
}


.form_title {
	color: #646cff;
	margin: 15px 7px 5px;
	font-weight: 600;
	font-size: 19px;
  }

.client_item_box{
	padding: 16px;
	margin: 2px;
}

.client_item_box:hover {
	background-color: #f1f1f1;
	cursor: pointer;
	border-radius: 7px;
}

.client_item_box .name{
	line-height: 21px;
	
}

.client_item_box:hover .name{
	color: #646cff;
}

.clients_header h2{
	margin: 7px 0 15px;
}






.pickBx{
	/*filter: drop-shadow(0 0 2em #f3f3f3);*/
	border-radius: 16px;
	min-height: 450px;
	min-width: 560px;
	background-color: rgba(255, 255, 255, 0.97);

}


.pickBx .header{
	padding: 7px 16px;
	margin: 0 16px;
}

.pickBx .header .bottom_loading{
	background-color: #afafaf;
	height: 1px;
	margin: 5px 16px 0;
}


.pickBx .header h3{
	margin: 0 3px;
}


@keyframes loadcenter {
	0% {
		width: 0;
	}
	50% {
		width: 50%;
	}
	100% {
		width: 100%;
	}
}


.pickBx .header
.bottom_loading.animate{
	height: 2px;
	margin: 4px auto 0;
	background-color: #646cff;
	animation: loadcenter .65s infinite alternate ease-in-out;
}





.client_item_pck{
	padding: 16px;
	margin: 3px 12px;
}

.client_item_pck:hover {
	background-color: #f1f1f1;
	cursor: pointer;
	border-radius: 7px;
}

.client_item_pck .name{
	line-height: 21px;
	
}

.client_item_pck:hover .name{
	color: #646cff;
}

.list_pick_scroll{
	overflow-y: auto;
	max-height: 380px;
}


.detailClient{
    max-width: 90%;
    margin: 0 auto;
}



.card_address {
    position: relative;
    border: 1px solid var(--color-setting-modal);
    width: 360px;
    border-radius: 7px;
    padding: 8px;
    color: var(--color-setting-modal);
	background: var(--bgcolor-setting-modal);
	margin: 9px;
}

.card_address {
    --color-setting-modal: var(--hrm_palette-blue-purple);
    --bgcolor-setting-modal: var(--hrm_palette-blue-purple_100);
}


.title_card{
	font-size: 15px;
    font-weight: 700;
    text-align: left;
    margin-left: 6px;
	font-family: "Google Sans","GoogleSans-Local";
	cursor: pointer;
}

.body_card{
	margin: 0;
	font-family: "Google Sans","GoogleSans-Local";
	margin-left: 40px;
}

.body_card .city_state{
	margin: 0;
	text-align: left;
	
}

.city_state .h4_gsans{
	margin: 0;
	text-align: left;
	cursor: pointer;
	margin-left: 4px;
	font-family: "Google Sans","GoogleSans-Local";
}

.city_state .h5_gsans{
	margin: 0;
	text-align: left;
	margin-left: 3px;
	font-weight: 700;
	font-family: "Google Sans","GoogleSans-Local";
	cursor: pointer;
}

.card_address svg{
    cursor: pointer;
}

.icon_action{
	width: 60px;
}



.icon_action .menu_icon,
.icon_action .menu_icon_acc
 {
	--menu_icon-opacity: opacity .05s ease-in;
	transition: var(--menu_icon-opacity);
	
}

.icon_action .menu_icon{
	opacity: 1 ;
	width: 100%;
}

.icon_action .menu_icon svg{
	margin-left: 30px;
}
.icon_action .menu_icon_acc
{
	opacity: 0 ;
	width: 0px;
	--menu_icon-opacity: opacity .000s ease-in;
	max-width: 0px;
}



.icon_action:hover .menu_icon {
	opacity: 0 ;
	width: 0px;
	max-width: 0px;
}

.icon_action:hover .menu_icon_acc{
	opacity: 1;
	z-index: 10;
	width: 100%;
	--menu_icon-opacity: opacity .15s ease-in;
	max-width: 60px;
}


.icon_action .menu_icon_acc svg {
	max-width: 0px;
}
.icon_action:hover .menu_icon_acc svg {
	max-width: 30px;
}

.parentsInfo_box{
	width: 100%;
}